	
export default ({name,data={}}) =>{
	// name,data
	return new Promise((resolve,reject)=>{
		const loading = data.isLoading;
		loading && delete data.isLoading
		!loading && uni.showLoading({})
		
					//
		uniCloud.callFunction({
			name,
			data,//
			success({result}){
				//result
				if (result.code == 0) {
					//
					resolve(result.data)
				}else{
					uni.showToast({
						//
						icon:'none',
						title:result.msg
					})
				}
			},
			fail(err){
				//
				reject(err)
			},
			complete(){
				//
				uni.hideLoading()//
			},
			// extensions
		})
	})
}