export default {
  install (Vue) {   // uniappinstallmixin
    Vue.mixin({
      data () {
        return {
			mobileReg:/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
          userRules:{
			  loginName:{
				  rules:[{
					  required:true,
					  'errorMessage':''
				  },{
					  validateFunction:this.validateLoginName
				  }]
			  },
			 password:{
			 		rules:[{
			 				required:true,
			 				'errorMessage':''
			 		}]
			 },
			 phone:{
			 				  rules:[{
			 					  required:true,
			 					  'errorMessage':''
			 				  },{
			 					  validateFunction:this.validatePhone
			 				  }]
			 },
			 vCode:{
			 				  rules:[{
			 					  required:true,
			 					  'errorMessage':''
			 				  },{
			 					  validateFunction:this.validateCode
			 				  }]
			 },
		  }
        }
      },
      methods: {
        async validateLoginName (rule,val,data,callback) {
		switch(true){
			case val.length < 6:
			callback('')
			break
			default :
			return true
			}
        },
		async validatePhone(rule,val,data,callback){
			switch(true){
				case !this.mobileReg.test(val) :
				callback('')
				break
				default :
				return true
				}
		},
		async validateCode(rule,val,data,callback){
			switch(true){
				case val != this.currentCode:
				callback('')
				break
				default :
				return true
				}
		}
      }
    })
  }
}